<template>
  <div class="app-links">
    <ul>
      <li v-for="link in linksleft" class="fl-left">
        <a href="#">
          {{link}}
        </a>
      </li>
      <li v-for="link in linksright" class="fl-right">
        <a href="#">
          {{link}}
        </a>
      </li>
    </ul>
  </div>
</template>

<script>

export default {
  name: 'app',
  data () {
    return {
      title: 'Test App',
      linksleft: ['Home','Dashboard','Help'],
      // linksright: ['Alerts','Settings','Logout']
      linksright: ['Logout','Settings','Alerts']
    }
  },
  methods: {

  },
  components: {
    // 'header': Header
  },
  filters: {
    reverse: function(arr) {
        // return arr.reverse();
        // slice to make a copy of array, then reverse the copy
        return value.slice().reverse();
    }
  }
}
</script>

<style scoped>
.app-links{
  /* background-color: rgb(163, 223, 51); */
}

ul {
    list-style-type: none;
    margin: 0;
    padding: 0;
    position: fixed;
    /* top: 100px; */
    left: 0;
    bottom: calc(100% - 100px);
    width: 100%;
    min-width: 600px;
    /* background-color: #333; */
    /* position: -webkit-sticky; /* Safari */
     /* position: sticky; */
}

li {
    float: left;
    color: white;
    background-color: #333;
}

li a {
    display: block;
    color: white;
    text-align: center;
    padding: 14px 16px;
    text-decoration: none;
}

/* Change the link color to #111 (black) on hover */
li a:hover {
    background-color: #111;
}

.active {
    background-color: #4CAF50;
}

/* Add a gray right border to all list items, except the last item (last-child) */
li {
    border-right: 1px solid #bbb;
}

li:nth-last-child(-n+3) {
  /* background-color: red; */
  /* float: right; */
  /* border-left: none; */
}
li.fl-right {
  float: right;
}

li:nth-last-child(+3) {
  /* background-color: red; */
  /* float: right; */
  /* border-right: none; */
}

/* li:last-child {
    border-right: none;
    float: right;
} */


</style>



