<template>
  <header class="">
    <app-title></app-title>
    <app-links></app-links>
  </header>
</template>

<script>
import AppLinks from './Links.vue'
import AppTitle from './Title.vue'

export default {
  name: 'app-header',
  data () {
    return {}
  },
  methods: {},
  components: {
    'app-links': AppLinks,
    'app-title': AppTitle
  }
}
</script>

<style scoped>
* {
  margin: 0;
  padding: 0;
}

header{
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100px;
  background-color: cyan;
  padding: 0px 0px 0px 20px;
  min-width: 600px;
}


</style>



