<template>
  <div class="app-nav">
    <!-- <p>Reports Nav</p> -->
    <ul>
      <li v-for="menuitem in menuitems" v-bind:class='isActive(menuitem.name)' v-on:click='setActive(menuitem.name)'>
        <!-- <a href="#"> -->
        <font-awesome-icon v-bind:icon="menuitem.icon" v-if="isCollapsed"/>
        <label v-if="!isCollapsed">
          {{menuitem.name}}
        </label>
        <!-- </a> -->
      </li>
    </ul>

    <!-- <div class="nav-toggle" v-on:click="isCollapsed = !isCollapsed"> -->
      <!-- <div class="" v-if="!isCollapsed">
        <font-awesome-icon icon="angle-double-left" v-for="count in 5"/>
      </div>

      <div class="" v-if="isCollapsed">
        <font-awesome-icon icon="angle-double-right"  v-for="count in 5"/>
      </div> -->

      <!-- <font-awesome-icon icon="bars" /> -->
      <!-- </div> -->
  </div>
</template>

<script>

export default {
  name: 'app-left-nav',
  data () {
    return {
      'menuitems': [
        {name:'Patients', icon: 'coffee'}
        ,{name: 'Prescribers', icon: 'user-md'}
        ,{name:'Drugs', icon: 'pills'}
        ,{name:'Insurance', icon: 'coffee'}
        ,{name:'Inventory', icon: 'coffee'}
        ,{name:'Plans', icon: 'coffee'}
        ,{name:'Users', icon: 'user'}
      ],
      'selectedmenuitem': 'ABC',
      'isCollapsed': false
    }
  },
  methods: {
    setActive: function(selecteditem) {
      console.log("User selected = "+selecteditem)
      console.log("Current ="+this.selectedmenuitem);
      if(this.selectedmenuitem != selecteditem){
        this.selectedmenuitem = selecteditem
      }
      console.log("New ="+this.selectedmenuitem)
    },
    isActive: function (selecteditem) {
      if(this.selectedmenuitem == selecteditem){
        return 'active'
      }
    }
  },
  components: {}
}
</script>

<style scoped>
* {
  margin: 0;
  padding: 0;
}

div.app-nav{
  background-color: rgb(139, 111, 238);
  position: fixed;
  top: 100px;
  left: 0;
  width: 150px;
  height: calc(100% - 150px);
  /* margin-right: calc(100% - 150px); */
  /* height: 80%; */
  min-width: inherit;
}

ul {
    list-style-type: none;
    /* background-color: #f1f1f1; */
    /* border: 3px solid #555; */
    /* position: fixed; /* Make it stick, even on scroll */ */
    overflow: auto; /* Enable scrolling if the sidenav has too much content */
}

li {
    display: block;
    padding: 15px;
    /* width: 150px; */
    /* text-align: center; */
    /* font-size: 100; */
    border-bottom: 1px solid black;
    text-decoration: none;
    color: black;
    font-weight: bold;
}

/* Change the link color on hover */
li:hover {
    background-color: green;
    /* text-decoration-color: white; */
    /* color: white; */
}

.active {
    background-color: white;
    color: black;
}
.active:hover {
  background-color: white;
}

.nav-toggle {
  position: fixed;
  left: 150px;
  /* top: 100px; */
  padding: 15px;
  background-color:  rgb(139, 111, 238);
  /* top: calc(100% - 70px); */

}


</style>



